footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg-yellow);
}

.footer-logo{
    font-size: 2rem;
    font-weight:500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.hr-line{
    height:100px;
    width: 100%;
    margin-bottom: 5rem;
   
}

.hr-line hr{
    height: 4rem;
    padding-left: 100px
}

.footer-socials{
   display: flex;
   justify-content: center;
   gap: 1rem;
   margin-bottom: 4rem;
}

.footer-socials a{
    background: var(--color-bg-yellow);
    color: var(--color-bg-blue);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer-socials a:hover{
    background: transparent;
    color: var(--color-bg-yellow);
    border-color: var(--color-bg-yellow);
}

.footer-copyright{
    margin: 4rem 0 10rem 0;
    color: var(--color-bg-yellow);
}

/* *******************MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width:600px){
    footer{
        width: 108%;
    }
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer-socials{
        margin-bottom: 2.6rem;
    }
}